  display: none;
}

:root {
  --link: blue;
  --text: #333;
  --text-muted: rgb(104, 103, 103);
  --bg: white;
  --font-monospace: "SFMono-Regular", Consolas, "Liberation Mono", Menlo,
    Courier, monospace;
  /* Tables */
  --tbl-a-color: rgb(66, 62, 62);
  --tbl-highlight-even: #e7e7e7;
  --tbl-highlight-odd: #fff;
  --tbl-hover: #eee;
  --th-padding: 0.4em 0.5em 0.25em 1em;
  --td-padding: 0.4em 0.5em 0.4em 1em;
  --btn-color: #027dc4;
  --btn-color-hover: #16a8fd;
  --btn-txt-color: #fff;
  --ring-color: rgba(51, 139, 255, 0.6);
  --border-color: rgb(185, 229, 255);
  --ref-padding: 0.05em 0.5em;
  --text-bg: var(--bg);
  --header-padding: .75em;
}

div#cgit {
  padding: 0em;
  margin: 0em;
  font-family: sans-serif;
  font-size: 11pt;
  color: var(--text);
  background: var(--bg);
}
div#cgit div.markdown-body {
  max-width: 800px;
  margin: 0 auto;
  padding: 2.3em;
  border-radius: 2px;
  box-shadow: -1px 1px 6px 2px rgba(0, 0, 0, 0.1);
}
div#cgit a {
  color: var(--link);
  text-decoration: none;
  border-radius: 0.2em;
  padding-left: 0.1em;
  padding-right: 0.1em;
}
div#cgit a:hover {
  text-decoration: underline;
}
div#cgit a.button {
  font-size: 90%;
  margin: 0em 0.5em;
}
div#cgit a.primary {
  font-size: 100%;
}
div#cgit a.secondary {
  font-size: 90%;
}
div#cgit a.branch-deco {
  color: #000;
  margin: 0px 0.5em;
  padding: var(--ref-padding);
  background-color: #88ff88;
  border: solid 1px #007700;
}
div#cgit a.tag-deco {
  color: #000;
  margin: 0px 0.5em;
  padding: var(--ref-padding);
  background-color: #ffff88;
  border: solid 1px #777700;
}
div#cgit a.tag-annotated-deco {
  color: #000;
  margin: 0px 0.5em;
  padding: var(--ref-padding);
  background-color: #ffcc88;
  border: solid 1px #777700;
}
div#cgit a.remote-deco {
  color: #000;
  margin: 0px 0.5em;
  padding: var(--ref-padding);
  background-color: #ccccff;
  border: solid 1px #000077;
}
div#cgit a.deco {
  color: #000;
  margin: 0px 0.5em;
  padding: var(--ref-padding);
  background-color: #ff8888;
  border: solid 1px #770000;
}
div#cgit table {
  border-collapse: collapse;
}
div#cgit table.tabs {
  border-collapse: collapse;
  margin-bottom: 0px;
  width: 100%;
  background-color: var(--border-color);
}
div#cgit table.tabs td {
  padding: 2.5em 0.5em 0.5em 0.5em;
  vertical-align: middle;
  border-radius: 1em;
}
div#cgit table.tabs td:first-of-type {
  padding-left: var(--header-padding);
}
div#cgit table.tabs td:last-of-type {
  padding-right: var(--header-padding);
}
div#cgit table.tabs td a {
  padding: 0.3em 0.75em;
  margin-left: 0.15em;
  margin-right: 0.15em;
  color: var(--tbl-a-color);
  font-size: 110%;
}
div#cgit table.tabs td a.active {
  color: #000;
  background-color: var(--bg);
}
div#cgit table.tabs td.form {
  text-align: right;
}
div#cgit table.tabs td.form form {
  padding-bottom: 0.2em;
  white-space: nowrap;
}
div#cgit table.list {
  width: 100%;
  border: none;
  border-collapse: collapse;
}
div#cgit table.list tr {
  background: var(--bg);
}
div#cgit table.list tr.logheader {
  background: #eee;
}
div#cgit table.list tr:nth-child(even) {
  background: var(--tbl-highlight-even);
}
div#cgit table.list tr:nth-child(odd) {
  background: var(--tbl-highlight-odd);
}
div#cgit table.list tr:hover {
  background: var(--tbl-hover);
}
div#cgit table.list tr.nohover {
  background: var(--bg);
}
div#cgit table.list tr.nohover:hover {
  background: var(--bg);
}
div#cgit table.list tr.nohover-highlight:hover:nth-child(even) {
  background: var(--tbl-highlight-even);
}
div#cgit table.list tr.nohover-highlight:hover:nth-child(odd) {
  background: var(--tbl-highlight-odd);
}
div#cgit table.list th {
  font-weight: bold;
  padding: var(--th-padding);
  vertical-align: baseline;
  border-bottom: 1.5px solid black;
}
div#cgit table.list th a {
  color: var(--text);
}
div#cgit table.list td {
  border: none;
  padding: var(--td-padding);
}
div#cgit table.list td.commitgraph {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  white-space: pre;
}
div#cgit table.list td.commitgraph .column1 {
  color: #a00;
}
div#cgit table.list td.commitgraph .column2 {
  color: #0a0;
}
div#cgit table.list td.commitgraph .column3 {
  color: #aa0;
}
div#cgit table.list td.commitgraph .column4 {
  color: #00a;
}
div#cgit table.list td.commitgraph .column5 {
  color: #a0a;
}
div#cgit table.list td.commitgraph .column6 {
  color: #0aa;
}
div#cgit table.list td.logsubject {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-weight: bold;
}
div#cgit table.list td.logmsg {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  white-space: pre;
  padding: 0 0.5em;
}
div#cgit table.list td a {
  color: black;
}
div#cgit table.list td a.ls-dir {
  font-weight: bold;
  color: #00f;
}
div#cgit table.list td a:hover {
  color: #00f;
}
div#cgit table.list td.reposection {
  font-style: italic;
  color: #888;
}
div#cgit table.list td.sublevel-repo {
  padding-left: 1.5em;
}
div#cgit table.blob {
  margin-top: 0.5em;
  border-top: solid 1px black;
}
div#cgit table.blob td.linenumbers {
  margin: 0;
  padding: 0 0.5em 0 0.5em;
  vertical-align: top;
  text-align: right;
  border-right: 1px solid gray;
}
div#cgit table.blob pre {
  padding: 0;
  margin: 0;
}
div#cgit table.blame td.linenumbers div.alt {
  padding: 0 0.5em 0 0;
}
div#cgit table.blame td.lines > div {
  position: relative;
}
div#cgit table.blame td.lines > div > pre {
  padding: 0 0 0 0.5em;
  position: absolute;
  top: 0;
}
div#cgit table.blame div.alt:nth-child(even) {
  background: #eee;
}
div#cgit table.blame div.alt:nth-child(odd) {
  background: white;
}
div#cgit table.bin-blob {
  margin-top: 0.5em;
  border: solid 1px black;
}
div#cgit table.bin-blob th {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  white-space: pre;
  border: solid 1px #777;
  padding: 0.5em 1em;
}
div#cgit table.bin-blob td {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  white-space: pre;
  border-left: solid 1px #777;
  padding: 0em 1em;
}
div#cgit table.nowrap td {
  white-space: nowrap;
}
div#cgit table.commit-info {
  border-collapse: collapse;
  margin-top: 1.5em;
}
div#cgit table.commit-info th {
  text-align: left;
  font-weight: normal;
  padding: 0.1em 1em 0.1em 0.1em;
  vertical-align: top;
}
div#cgit table.commit-info td {
  font-weight: normal;
  padding: 0.1em 1em 0.1em 0.1em;
}
div#cgit table.diffstat {
  border-collapse: collapse;
  border: solid 1px #aaa;
  background-color: #eee;
}
div#cgit table.diffstat th {
  font-weight: normal;
  text-align: left;
  text-decoration: underline;
  padding: 0.1em 1em 0.1em 0.1em;
  font-size: 100%;
}
div#cgit table.diffstat td {
  padding: 0.2em 0.2em 0.1em 0.1em;
  font-size: 100%;
  border: none;
}
div#cgit table.diffstat td.mode {
  white-space: nowrap;
}
div#cgit table.diffstat td span.modechange {
  padding-left: 1em;
  color: red;
}
div#cgit table.diffstat td.add a {
  color: green;
}
div#cgit table.diffstat td.del a {
  color: red;
}
div#cgit table.diffstat td.upd a {
  color: blue;
}
div#cgit table.diffstat td.graph {
  width: 500px;
  vertical-align: middle;
}
div#cgit table.diffstat td.graph table {
  border: none;
}
div#cgit table.diffstat td.graph td {
  padding: 0px;
  border: 0px;
  height: 7pt;
}
div#cgit table.diffstat td.graph td.add {
  background-color: #5c5;
}
div#cgit table.diffstat td.graph td.rem {
  background-color: #c55;
}
div#cgit table.diff {
  width: 100%;
}
div#cgit table.diff td {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  white-space: pre;
}
div#cgit table.diff td div.head {
  font-weight: bold;
  margin-top: 1em;
  color: black;
}
div#cgit table.diff td div.hunk {
  color: #009;
}
div#cgit table.diff td div.add {
  color: green;
}
div#cgit table.diff td div.del {
  color: red;
}
div#cgit table.stats {
  border: solid 1px black;
  border-collapse: collapse;
}
div#cgit table.stats th {
  text-align: left;
  padding: 1px 0.5em;
  background-color: #eee;
  border: solid 1px black;
}
div#cgit table.stats td {
  text-align: right;
  padding: 1px 0.5em;
  border: solid 1px black;
}
div#cgit table.stats td.total {
  font-weight: bold;
  text-align: left;
}
div#cgit table.stats td.sum {
  color: #c00;
  font-weight: bold;
}
div#cgit table.stats td.left {
  text-align: left;
}
div#cgit table.vgraph {
  border-collapse: separate;
  border: solid 1px black;
  height: 200px;
}
div#cgit table.vgraph th {
  background-color: #eee;
  font-weight: bold;
  border: solid 1px white;
  padding: 1px 0.5em;
}
div#cgit table.vgraph td {
  vertical-align: bottom;
  padding: 0px 10px;
}
div#cgit table.vgraph div.bar {
  background-color: #eee;
}
div#cgit table.hgraph {
  border: solid 1px black;
  width: 800px;
}
div#cgit table.hgraph th {
  background-color: #eee;
  font-weight: bold;
  border: solid 1px black;
  padding: 1px 0.5em;
}
div#cgit table.hgraph td {
  vertical-align: middle;
  padding: 2px 2px;
}
div#cgit table.hgraph div.bar {
  background-color: #eee;
  height: 1em;
}
div#cgit table.ssdiff {
  width: 100%;
}
div#cgit table.ssdiff td {
  font-size: 75%;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  white-space: pre;
  padding: 1px 4px 1px 4px;
  border-left: solid 1px #aaa;
  border-right: solid 1px #aaa;
}
div#cgit table.ssdiff td.add {
  color: black;
  background: #cfc;
  min-width: 50%;
}
div#cgit table.ssdiff td.add_dark {
  color: black;
  background: #aca;
  min-width: 50%;
}
div#cgit table.ssdiff td.del {
  color: black;
  background: #fcc;
  min-width: 50%;
}
div#cgit table.ssdiff td.del_dark {
  color: black;
  background: #caa;
  min-width: 50%;
}
div#cgit table.ssdiff td.changed {
  color: black;
  background: #ffc;
  min-width: 50%;
}
div#cgit table.ssdiff td.changed_dark {
  color: black;
  background: #cca;
  min-width: 50%;
}
div#cgit table.ssdiff td.lineno {
  color: black;
  background: #eee;
  text-align: right;
  width: 3em;
  min-width: 3em;
}
div#cgit table.ssdiff td.hunk {
  color: black;
  background: #ccf;
  border-top: solid 1px #aaa;
  border-bottom: solid 1px #aaa;
}
div#cgit table.ssdiff td.head {
  border-top: solid 1px #aaa;
  border-bottom: solid 1px #aaa;
}
div#cgit table.ssdiff td.head div.head {
  font-weight: bold;
  color: black;
}
div#cgit table.ssdiff td.foot {
  border-top: solid 1px #aaa;
  border-left: none;
  border-right: none;
  border-bottom: none;
}
div#cgit table.ssdiff td.space {
  border: none;
}
div#cgit table.ssdiff td.space div {
  min-height: 3em;
}
div#cgit table.ssdiff span.add {
  background: #cfc;
  font-weight: bold;
}
div#cgit table.ssdiff span.del {
  background: #fcc;
  font-weight: bold;
}
div#cgit table#header {
  width: 100%;
  background-color: var(--border-color);
}
div#cgit table#header tr:first-of-type {
  padding-top: var(--header-padding);
}
div#cgit table#header td {
  padding: 0.2rem;
}
div#cgit table#header td:first-of-type {
  padding-left: var(--header-padding);
}
div#cgit table#header td:last-of-type {
  padding-right: var(--header-padding);
}
div#cgit table#header td.logo {
  width: 96px;
  vertical-align: top;
}
div#cgit table#header td.main {
  font-size: 250%;
  padding-left: 10px;
  white-space: nowrap;
}
div#cgit table#header td.main a {
  color: #000;
}
div#cgit table#header td.form {
  text-align: right;
  vertical-align: bottom;
  padding-right: 1em;
  padding-bottom: 2px;
  white-space: nowrap;
}
div#cgit table#header td.sub {
  color: var(--text-muted);
  border-top: solid 1px #ccc;
  padding-left: 10px;
  line-height: 1.3;
}
div#cgit div.path {
  margin: 0px;
  padding: 5px 2em 2px 2em;
  color: var(--text);
  background-color: var(--path-bg);
}
div#cgit div.content {
  margin: 0 auto;
  padding: 2em;
}
div#cgit div.error {
  color: red;
  font-weight: bold;
  margin: 1em 2em;
}
div#cgit div.cgit-panel {
  float: right;
  margin-top: 1.5em;
}
div#cgit div.cgit-panel table {
  border-collapse: collapse;
  border: solid 1px #aaa;
  background-color: #eee;
}
div#cgit div.cgit-panel th {
  text-align: center;
}
div#cgit div.cgit-panel td {
  padding: 0.25em 0.5em;
}
div#cgit div.cgit-panel td.label {
  padding-right: 0.5em;
}
div#cgit div.cgit-panel td.ctrl {
  padding-left: 0.5em;
}
div#cgit div.commit-subject {
  font-weight: bold;
  font-size: 125%;
  margin: 1.5em 0em 0.5em 0em;
  padding: 0em;
}
div#cgit div.commit-msg {
  white-space: pre;
  font-family: var(--font-monospace);
}
div#cgit div.notes-header {
  font-weight: bold;
  padding-top: 1.5em;
}
div#cgit div.notes {
  white-space: pre;
  font-family: var(--font-monospace);
  border: solid 1px #ee9;
  background-color: #ffd;
  padding: 0.3em 2em 0.3em 1em;
  float: left;
}
div#cgit div.notes-footer {
  clear: left;
}
div#cgit div.diffstat-header {
  font-weight: bold;
  padding-top: 1.5em;
}
div#cgit div.diffstat-summary {
  color: #888;
  padding-top: 0.5em;
}
div#cgit div.footer {
  padding: 0.75em;
  background-color: var(--border-color);
  text-align: center;
  font-size: 90%;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
}
div#cgit div.footer a {
  color: var(--text);
  text-decoration: underline;
}
div#cgit div.footer a:hover {
  color: var(--link);
  text-decoration: underline;
}
div#cgit img {
  border: none;
}
div#cgit input#switch-btn {
  margin: 2px 0px 0px 0px;
}
div#cgit td#sidebar input.txt {
  width: 100%;
  margin: 2px 0px 0px 0px;
}
div#cgit table#grid {
  margin: 0px;
}
div#cgit td#content {
  vertical-align: top;
  padding: 1em 2em 1em 1em;
  border: none;
}
div#cgit div#summary {
  vertical-align: top;
  margin-bottom: 1em;
}
div#cgit table#downloads {
  float: right;
  border-collapse: collapse;
  border: solid 1px #777;
  margin-left: 0.5em;
  margin-bottom: 0.5em;
}
div#cgit table#downloads th {
  background-color: #ccc;
}
div#cgit div#blob {
  border: solid 1px black;
}
div#cgit td.ls-size {
  text-align: right;
  font-family: var(--font-monospace);
  width: 10em;
}
div#cgit td.ls-mode {
  font-family: var(--font-monospace);
  width: 10em;
}
div#cgit .oid {
  font-family: var(--font-monospace);
  font-size: 90%;
}
div#cgit .left {
  text-align: left;
}
div#cgit .right {
  text-align: right;
}
div#cgit ul.pager {
  list-style-type: none;
  text-align: center;
  margin: 1em 0em 0em 0em;
  padding: 0;
}
div#cgit ul.pager li {
  display: inline-block;
  margin: 0.25em 0.5em;
}
div#cgit ul.pager a {
  color: #777;
}
div#cgit ul.pager .current {
  font-weight: bold;
}
div#cgit span.age-mins {
  font-weight: bold;
  color: #080;
}
div#cgit span.age-hours {
  color: #080;
}
div#cgit span.age-days {
  color: #040;
}
div#cgit span.age-weeks {
  color: #444;
}
div#cgit span.age-months {
  color: #888;
}
div#cgit span.age-years {
  color: #bbb;
}
div#cgit span.insertions {
  color: #080;
}
div#cgit span.deletions {
  color: #800;
}

div#cgit table.tabs a[href^="http://"]:after, div#cgit table.tabs a[href^="https://"]:after {
  content: url(data:image/png;base64,);
  opacity: 0.5;
  margin: 0 0 0 5px;
}

div#cgit table.blob td.hashes,
div#cgit table.blob td.lines {
  margin: 0;
  padding: 0 0 0 0.5em;
  vertical-align: top;
  color: black;
}

div#cgit table.blob td.linenumbers a,
div#cgit table.ssdiff td.lineno a {
  color: gray;
  text-align: right;
  text-decoration: none;
}

div#cgit table.blob td.linenumbers a:hover,
div#cgit table.ssdiff td.lineno a:hover {
  color: black;
}

div#cgit table.blame td.hashes,
div#cgit table.blame td.lines,
div#cgit table.blame td.linenumbers {
  padding: 0;
}

div#cgit table.blame td.hashes div.alt,
div#cgit table.blame td.lines div.alt {
  padding: 0 0.5em 0 0.5em;
}

div#cgit div.commit-subject a.branch-deco,
div#cgit div.commit-subject a.tag-deco,
div#cgit div.commit-subject a.tag-annotated-deco,
div#cgit div.commit-subject a.remote-deco,
div#cgit div.commit-subject a.deco {
  margin-left: 1em;
  font-size: 75%;
}

:root {
  tab-size: 4;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
}

a:focus,
button:focus,
input:focus,
select:focus {
  box-shadow: 0 0 0 3px var(--ring-color);
  outline: none;
  border-color: var(--text);
}

select,
input {
  background-color: var(--bg);
  outline: none;
  box-shadow: none;
  border-radius: 0.25em;
  padding: 0.2em 0.45em;
  margin-left: 0.15em;
  margin-right: 0.15em;
  border: solid 1px var(--btn-color);
}

button,
input[type=submit] {
  background-color: var(--btn-color);
  color: var(--btn-txt-color);
  outline: none;
  border: none;
  margin-left: 0.15em;
  margin-right: 0.15em;
  border-radius: 0.25em;
  padding: 0.35em 0.75em;
}
button:hover, button:active,
input[type=submit]:hover,
input[type=submit]:active {
  background-color: var(--btn-color-hover);
}

table[summary="tree listing"] td:last-child,
th:last-child {
  text-align: right;
}